import React, { useEffect, Suspense, lazy, Component } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import { AuthProvider } from "./contexts/AuthContext";
import { CurrencyProvider } from "./contexts/CurrencyContext";
//  [] ItemProvider AlchanLayout  (  )
import { ThemeProvider } from "./contexts/ThemeContext";
import { SkeletonPage } from "./components/ui/Skeleton";

//  React Query   - Firestore   
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      //    : 30 (Firestore  )
      staleTime: 30 * 60 * 1000,
      //    : 2
      gcTime: 2 * 60 * 60 * 1000,
      //      refetch  ( )
      refetchOnWindowFocus: false,
      //     refetch 
      refetchOnReconnect: false,
      //     refetch  ( )
      refetchOnMount: false,
      //     1
      retry: 1,
      //     
      retryOnMount: false,
    },
  },
});

//  ChunkLoadError  - lazy import   1   
function lazyWithRetry(importFn) {
  return lazy(() =>
    importFn().catch(() => {
      //      throw
      const reloaded = sessionStorage.getItem("chunk_reload");
      if (reloaded) {
        sessionStorage.removeItem("chunk_reload");
        return importFn(); //  
      }
      sessionStorage.setItem("chunk_reload", "1");
      window.location.reload();
      return new Promise(() => {}); //   pending 
    }),
  );
}

//   -   
const AlchanLayout = lazyWithRetry(() => import("./components/AlchanLayout"));
const Login = lazyWithRetry(() => import("./pages/auth/Login"));
const PrivacyPolicy = lazyWithRetry(
  () => import("./pages/legal/PrivacyPolicy"),
);
const ConsentForm = lazyWithRetry(() => import("./pages/legal/ConsentForm"));

//   (Tailwind   Tailwind  )
import "./styles.css";
import "./App.css";
import "./index.css"; // Tailwind CSS -  import  
import { logger } from "./utils/logger";

//    - PWA  
class ErrorBoundary extends Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false, error: null };
  }

  static getDerivedStateFromError(error) {
    return { hasError: true, error };
  }

  componentDidCatch(error, errorInfo) {
    logger.error("[]   :", error, errorInfo);

    // ChunkLoadError   (       )
    const errorString = error?.toString() || "";
    if (
      errorString.includes("ChunkLoadError") ||
      errorString.includes("Loading chunk") ||
      error?.name === "ChunkLoadError"
    ) {
      logger.log("[] ChunkLoadError  -  ");
      const reloaded = sessionStorage.getItem("chunk_reload");
      if (!reloaded) {
        sessionStorage.setItem("chunk_reload", "1");
        window.location.reload();
        return;
      }
      sessionStorage.removeItem("chunk_reload");
      this.clearCachesAndReload();
      return;
    }

    // IndexedDB       
    if (
      errorString.includes("IndexedDB") ||
      errorString.includes("QuotaExceeded") ||
      errorString.includes("SecurityError") ||
      errorString.includes("InvalidStateError")
    ) {
      logger.log("[]    -  ");
      this.clearCachesAndReload();
    }
  }

  clearCachesAndReload = async () => {
    try {
      // IndexedDB 
      if (window.indexedDB) {
        const databases = (await window.indexedDB.databases?.()) || [];
        databases.forEach((db) => {
          if (db.name) {
            window.indexedDB.deleteDatabase(db.name);
          }
        });
      }

      //  
      if ("caches" in window) {
        const names = await caches.keys();
        await Promise.all(names.map((name) => caches.delete(name)));
      }

      //  
      localStorage.clear();
      sessionStorage.clear();

      // 
      window.location.reload();
    } catch (e) {
      logger.error("[]   :", e);
      window.location.reload();
    }
  };

  render() {
    if (this.state.hasError) {
      return (
        <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-br from-[#0a0a12] to-[#1a1a2e] text-white p-5 text-center">
          <div className="text-5xl mb-4"></div>
          <h1 className="text-2xl mb-2">
                
          </h1>
          <p className="mb-6 opacity-80">    ...</p>
          <button
            onClick={this.clearCachesAndReload}
            className="px-6 py-3 bg-white text-indigo-600 border-0 rounded-xl text-base font-bold cursor-pointer"
          >
              
          </button>
        </div>
      );
    }

    return this.props.children;
  }
}

function App() {
  // FCM    
  useEffect(() => {
    if ("serviceWorker" in navigator) {
      navigator.serviceWorker.ready
        .then((registration) => {
          if (registration.active) {
            const firebaseConfig = {
              apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
              authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
              projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
              storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
              messagingSenderId:
                process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
              appId: process.env.REACT_APP_FIREBASE_APP_ID,
            };
            registration.active.postMessage({
              type: "FIREBASE_CONFIG",
              config: firebaseConfig,
            });
          }
        })
        .catch((err) => {
          logger.warn("[]    ():", err);
        });
    }
  }, []);

  return (
    <ErrorBoundary>
      <QueryClientProvider client={queryClient}>
        <ThemeProvider>
          <AuthProvider>
            <CurrencyProvider>
              {/*  [] ItemProvider  - AlchanLayout      */}
              <Router>
                <Suspense fallback={<SkeletonPage />}>
                  <Routes>
                    <Route path="/login" element={<Login />} />
                    <Route path="/privacy" element={<PrivacyPolicy />} />
                    <Route path="/consent-form" element={<ConsentForm />} />
                    <Route path="/*" element={<AlchanLayout />} />
                  </Routes>
                </Suspense>
              </Router>
            </CurrencyProvider>
          </AuthProvider>
        </ThemeProvider>
      </QueryClientProvider>
    </ErrorBoundary>
  );
}

export default App;
